<template>
<div>
  <nav class="navbar navbar-default navbar-fixed-top">
    <div class="container">
      <!-- Brand and toggle get grouped for better mobile display -->
      <div class="navbar-header">
        <button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1" aria-expanded="false">
                      <span class="sr-only">Toggle navigation</span>
                      <span class="icon-bar"></span>
                      <span class="icon-bar"></span>
                      <span class="icon-bar"></span>
        </button>
        <router-link to='/'>
        <a class="navbar-brand">中南最前线</a>
        </router-link>
      </div>
      <!-- Collect the nav links, forms, and other content for toggling -->
      <div class="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
        <ul class="nav navbar-nav">
          <li @click='sss'><router-link :to='currenturl'>弹窗 </router-link></li>
          <li @click='show_currentevent'><router-link :to='currenturl'>个人信息测试 </router-link></li>
        </ul>
        <ul class="nav navbar-nav navbar-right">
          <li v-if='haslogin'><router-link :to='currenturl'>个人信息 </router-link></li>
          <li v-if='haslogin' @click='logout'><router-link :to='currenturl'>退出 </router-link></li>
          <li v-if='!haslogin' data-toggle="modal" data-target="#myModal" @click='login'><router-link :to='currenturl'>登录 </router-link></li>
          <li v-if='!haslogin' data-toggle="modal" data-target="#myModal" @click='regis'><router-link :to='currenturl'>注册 </router-link></li>
        </ul>
      </div>
      <!-- /.navbar-collapse -->
      <!-- Button trigger modal -->
    </div>
    <!-- /.container-fluid -->
  </nav>
        <div class="modal fade" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true" data-backdrop="static">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <button id='closemodel' type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
              <h4 class="modal-title" id="myModalLabel">{{ login_title }}</h4>
            </div>
            <div class="modal-body">
              <div class="input-group col-xs-10">
                <span class="input-group-addon">用户名 </span>
                <input v-model='uname' type="text" class="form-control" placeholder="邮箱" aria-describedby="sizing-addon2">
              </div>
              <div style='height: 20px'></div>
              <div class="input-group col-xs-10">
                <span class="input-group-addon">密码 </span>
                <input v-model='psword' type="password" class="form-control" placeholder="英文加数字" aria-describedby="sizing-addon2">
              </div>
              <div v-show='show_nickname'>
              <div style='height: 20px'></div>
              <div class="input-group col-xs-10">
                <span class="input-group-addon">昵称 </span>
                <input v-model='nickname' type="text" class="form-control" placeholder="昵称" aria-describedby="sizing-addon2">
              </div>
              </div>
            </div>
            <div class="modal-footer" id='twobtn'>
              <button type="button" class="btn btn-primary" @click='sub'>{{ login_title }}</button>
              <!-- <div class='jumbotron'>调试用,正式版本删除<br>{{ajax_data}}</div> -->
            </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        uname: '',
        psword: '',
        nickname: '',
        show_nickname: false,
        login_title: '',
        currenturl: ''
      }
    },
    computed: { // 绑定更新ajax_data
      haslogin: function () {
        return this.$store.state.haslogin
      },
      ajax_data: function () {
        const type = this.$store.state.opt.params.type
        if (type === 'USER_REGISTE') {
          return {
            username: this.uname,
            password: this.psword,
            nickname: this.nickname
          }
        } else if (type === 'USER_LOGIN') {
          return {
            username: this.uname,
            password: this.psword
          }
        } else if (type === 'USER_CHECK') {
          return {}
        } else {
          return {}
        }
      }
    },
    methods: {
      sub () {
        this.$store.commit('ajax_data', this.ajax_data)
        this.$store.dispatch('ajax_start')
      },
      regis () {
        this.show_nickname = true
        this.login_title = '注册'
        this.$store.commit('set_ajax', {
          t: 'USER_REGISTE',
          s: (data) => {
            console.log(data)
            this.$swal(data.message)
            document.getElementById('closemodel').click()     // 待解决  直接操作了DOM   不符合MVVM理念
            this.uname = ''
            this.psword = ''
            this.nickname = ''
          },
          f: (data) => {
            console.log(data)
            this.$swal(data.message)
          }
        })
      },
      login () {
        this.show_nickname = false
        this.login_title = '登录'
        this.$store.commit('set_ajax', {
          t: 'USER_LOGIN',
          s: (data) => {
            console.log(data)
            this.$swal(data.message)
            document.getElementById('closemodel').click()     // 待解决  直接操作了DOM   不符合MVVM理念
            this.uname = ''
            this.psword = ''
            this.nickname = ''
            this.$store.dispatch('checklogin')
          },
          f: (data) => {
            console.log(data)
            this.$swal(data.message)
          }
        })
      },
      logout () {
        this.$store.commit('set_ajax', {
          t: 'USER_QUIT',
          s: (data) => {
            console.log(data)
            this.$store.dispatch('checklogin')
          },
          f: (data) => {
            console.log(data)
            this.$store.dispatch('checklogin')
          }
        })
        this.$store.dispatch('ajax_start')
      },
      sss () {
        this.$swal({
          title: '成功',
          text: '不允许外部关闭',
          type: 'success',
          confirmButtonText: 'Submit',
          showLoaderOnConfirm: true,
          allowOutsideClick: false,
          preConfirm: function (text) {
            return new Promise(function (resolve, reject) {
              setTimeout(function () {
                resolve()
              }, 1000)
            })
          }
        }).then((data) => {
          this.$swal(data)
        }, (data) => {
          console.log(data)
        })
      },
      show_currentevent () {
        this.$store.state.show_currentevent = !this.$store.state.show_currentevent
      }
    },
    watch: {
      $route: function () {
        this.currenturl = this.$route.path
      }
    }
  }
</script>

<style scoped>
  .modal-dialog {
    top: 30%;
    padding-left: 50px;
    padding-right: 50px
  }
  #myModalLabel,
  #twobtn {
    text-align: center
  }
</style>



