<template>
  <div>
    <div class='row maptab'>
      <div @click='enter(pos.xinxiao)' class="col-xs-2">
              <tag txt='新校'></tag>
            </div>
            <div @click='enter(pos.nanxiao)' class="col-xs-2">
              <tag txt='南校'></tag>
            </div>
            <div @click='enter(pos.benbu)' class="col-xs-2">
              <tag txt='本部'></tag>
            </div>
            <div @click='enter(pos.xiangya)' class="col-xs-2">
              <tag txt='湘雅'></tag>
            </div>
            <div @click='enter(pos.tiedao)' class="col-xs-2">
              <tag txt='铁道'></tag>
            </div>
              <button @click='create_new_event' class="btn btn-danger col-md-1">发布 </button>
    </div>
    <baidu-map class='bm' :center='position.center' @zoomend='synczoom' @moveend='sync' :scroll-wheel-zoom='scroll' :zoom='position.zoom'>
    <bm-navigation anchor="BMAP_ANCHOR_BOTTOM_LEFT"></bm-navigation>
    <bm-marker v-if='centerlabel' 
    :icon="{url: './static/img/arrow.ico', size: {width: 64, height: 64}}" 
    :position="position.center" 
    :dragging='dragging' 
    animation="BMAP_ANIMATION_DROP" 
    @rightclick='get_pos'>
    <bm-info-window :show='tips'>拖动我选择地点，右键点我确认</bm-info-window>
    </bm-marker>
    <template v-for="item in init_event_data">
    <bm-marker 
    :key='item.eventid'
    :position='{lng: item.longitude, lat: item.latitude}' 
    @click='show_event($event,item)' 
    :offset='{width: 0, height: 12.5}'
    >
    </bm-marker>
    <bm-label 
    :content='item.title' 
    :labelStyle="{color: '#000', fontSize : '20px', backgroundColor: 'transparent', borderColor: 'transparent'}"
    :position='{lng: item.longitude, lat: item.latitude}' 
    ></bm-label>
    </template>
    </baidu-map>
  </div>
</template>

<script>
  import tag from '@/components/tag'
  export default {
    data () {
      return {
        position: {// 点击定位一次后若鼠标拖动缩放   再点击相同地点  会出现错误
          center: {
            lng: 112.942658,
            lat: 28.164901
          },
          zoom: 15
        },
        dragging: true,
        scroll: true,
        centerlabel: false,
        tips: true,
        event_title: '',
        eventid_to_show: '',
        pos: {
          nanxiao: {
            lng: 112.942658,
            lat: 28.164901
          },
          xinxiao: {
            lng: 112.94644,
            lat: 28.154619
          },
          benbu: {
            lng: 112.935229,
            lat: 28.177018
          },
          xiangya: {
            lng: 112.949436,
            lat: 28.225955
          },
          tiedao: {
            lng: 112.999247,
            lat: 28.144036
          }
        },
        init_event_data: {}
      }
    },
    computed: {
      editing () {
        return this.$store.state.editing
      },
      ajax_data () {
        const type = this.$store.state.opt.params.type
        if (type === 'EVENT_INIT') {
          return {
            happentime1: '2000-01-01',
            happentime2: '2017-07-01'
          }
        } else if (type === 'EVENT_GETMESSAGE') {
          return {
            eventid: this.eventid_to_show
          }
        } else {
          return {}
        }
      }
    },
    mounted: function () {
      const a = this.$store
      a.commit('set_ajax', {
        t: 'EVENT_INIT',
        s: (data) => {
          this.init_event_data = data.data
          console.log(this.init_event_data)
        },
        f: (data) => {
          console.log(data)
        }
      })
      a.commit('ajax_data', this.ajax_data)
      a.dispatch('ajax_start').then(() => {
        a.dispatch('checklogin')
      }, (error) => {
        console.log(error)
      })
    },
    methods: {
      handler ({
        BMap,
        map
      }) {
        console.log(BMap, map)
        this.lng = 116.404
        this.lat = 39.915
      },
      sync (e) {
        this.position.center = e.target.getCenter()
      },
      synczoom (e) {
        this.position.zoom = e.target.getZoom()
      },
      enter (target) {
        setTimeout(() => {
          this.position.center = target
        }, 100)
        setTimeout(() => {
          this.position.zoom = 17.9
        }, 100)
        setTimeout(() => {
          this.position.zoom = 18
        }, 100)
      },
      create_new_event () {
        if (!this.$store.state.haslogin) {
          this.$swal({
            type: 'warning',
            text: '请先登录再进行操作'
          })
        } else
        if (!this.editing && this.centerlabel === false) {
          this.$swal.queue([{
            progressSteps: ['1', '2'],
            title: '第一步',
            text: '请输入事件标题',
            confirmButtonText: '下一步',
            input: 'text',
            preConfirm: (title) => {
              return new Promise((resolve, reject) => {
                if (title === '') {
                  reject('标题不能为空！')
                } else {
                  resolve() // 默认参数有 input
                }
              })
            }
          }, {
            progressSteps: ['1', '2'],
            title: '第二步',
            text: '请在下面的地图中选取事件发生的地点',
            confirmButtonText: '去选取'
          }]).then((data) => {
            console.log(data)
            this.centerlabel = true
            this.event_title = data[0]
            setTimeout(() => {
              this.tips = false
            }, 3000)
          }).catch(this.$swal.noop)
        } else if (this.centerlabel === true) { // 当开始选择地点又还没右键确定的时候如果再点击发布
          this.$swal({
            type: 'warning',
            text: '请在下面的地图中选取事件发生的地点',
            confirmButtonText: '确定'
          })
        } else {
          this.$router.push('edit')
        }
      },
      get_pos (event) {
        console.log(event.point)
        this.$router.push({
          name: 'edit', // 只能用name来传params，如果用path数据就传不过去  也不知道为什么。
          params: {
            location: event.point,
            title: this.event_title
          }
        })
        this.$store.commit('startedit')
        this.centerlabel = false
        this.tips = true
      },
      show_event (event, item) {
        const a = this.$store
        a.commit('set_ajax', {
          t: 'EVENT_GETMESSAGE',
          s: (data) => {
            // this.$swal({
            //   title: data.data.title,
            //   width: 1500,
            //   confirmButtonText: '看完了',
            //   allowOutsideClick: false,
            //   html: data.data.happentime + data.data.event
            // })
            console.log(data.data)
            this.$store.commit('set_current_event', data.data)
            this.$store.state.show_currentevent = !this.$store.state.show_currentevent
          },
          f: (data) => {
            console.log(data)
          }
        })
        this.eventid_to_show = item.eventid
        a.commit('ajax_data', this.ajax_data)
        a.dispatch('ajax_start').then((data) => {
          console.log(data, 'success')
        }, (error) => {
          console.log(error)
        })
      }
    },
    components: {
      tag
    }
  }
</script>

<style>
  .bm {
    position: absolute;
    top: 100px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    box-shadow: 0 4px 5px 0 rgba(0, 0, 0, .14), 0 1px 10px 0 rgba(0, 0, 0, .12), 0 2px 4px -1px rgba(0, 0, 0, .2);
    -webkit-box-shadow: 0 4px 5px 0 rgba(0, 0, 0, .14), 0 1px 10px 0 rgba(0, 0, 0, .12), 0 2px 4px -1px rgba(0, 0, 0, .2)
  }
  .maptab {
    position: fixed;
    top: 61px;
    left: 0;
    right: 0;
  }
  .col-xs-2 {
    text-align: center
  }

  label {
    max-width:none
  }
</style>



