<template>
    <div class="draggable-header-view"
    @mousedown="startDrag" @touchstart="startDrag"
    @mousemove="onDrag" @touchmove="onDrag"
    @mouseup="stopDrag" @touchend="stopDrag" 
    @mouseleave="stopDrag"
    :style='{width: width+"px"}'
    >
    <svg class="bg" width="100%" height="100%">
      <path :d="headerPath" fill="#5bc0de"></path>
    </svg>
    <div class="header">
      <slot name="header"></slot>
    </div>
    <div class="content" :style="contentPosition">
      <slot name="content"></slot>
    </div>
  </div>
</template>

<script>
import dynamics from 'dynamics.js'
export default {
  name: 'draggable-header-view',
  data: function () {
    return {
      dragging: false,
      // record drag start point
      c: {   // quadratic bezier control point
        x: 0,
        y: 200
      },
      start: { x: 0, y: 0 },
      width: ''
    }
  },
  watch: {
    show_currentevent: function () {
      this.width = this.$parent.$el.lastChild.offsetWidth // 一顿骚操作，总算得到了背景的宽度
      this.c.x = this.width
    }
  },
  computed: {
    contentPosition: function () {
      var dy = this.c.y - 200
      var dampen = dy > 0 ? 2 : 4
      return {
        transform: 'translate3d(0,' + dy / dampen + 'px,0)'
      }
    },
    show_currentevent: function () {
      return this.$store.state.show_currentevent
    },
    headerPath: function () {
      return 'M0,0 L' + this.width + ',0 L' + this.width + ',200 ' +
      'Q' + this.c.x + ',' + this.c.y + ' 0,200'
    }
  },
  methods: {
    startDrag: function (e) {
      e = e.changedTouches ? e.changedTouches[0] : e
      this.dragging = true
      this.start.x = e.pageX
      this.start.y = e.pageY
    },
    onDrag: function (e) {
      e = e.changedTouches ? e.changedTouches[0] : e
      if (this.dragging) {
        this.c.x = this.width / 2 + (e.pageX - this.start.x)
        // dampen vertical drag by a factor
        var dy = e.pageY - this.start.y
        var dampen = dy > 0 ? 1.5 : 4
        this.c.y = 200 + dy / dampen
      }
    },
    stopDrag: function () {
      if (this.dragging) {
        this.dragging = false
        dynamics.animate(this.c, {
          x: this.width / 2,
          y: 200
        }, {
          type: dynamics.spring,
          duration: 700,
          friction: 280
        })
      }
    }
  }
}
</script>

<style>
h1 {
  font-weight: 300;
  font-size: 1.8em;
  margin-top: 0;
}
a {
  color: #fff;
}
.draggable-header-view {
  background-color: #fff;
  box-shadow: 0 4px 16px rgba(0,0,0,.15);
  width: 800px;
  height: 100%;
  overflow: hidden;
  position: fixed;
  font-family: 'Roboto', Helvetica, Arial, sans-serif;
  color: #fff;
  font-size: 14px;
  font-weight: 300;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

@media only screen and (max-width: 420px) {
  .draggable-header-view {
    width: 300px;
  }
}

.draggable-header-view .bg {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}
.draggable-header-view .header, .draggable-header-view .content {
  position: relative;
  z-index: 1;
  padding: 30px;
  box-sizing: border-box;
}
.draggable-header-view .header {
  height: 200px;
}
.draggable-header-view .content {
  color: #333;
  line-height: 1.5em;
}
</style>



