      <transition name='slide' mode='in-out'>
  <router-view></router-view>
      </transition>
    </keep-alive>
    <event></event>
  </div>
</template>

<script>
import event from '@/components/event'
export default {
  name: 'background',
  data () {
    return {
    }
  },
  components: {
    event
  }
