}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.slide-enter-active {
    animation: slide-in .5s ease;
  }
  .slide-leave-active {
    animation: slide-out .5s ease;
  }
  @keyframes slide-in {
    0% {
      transform: translateX(-100%);
    }
    100% {
      transform: translateX(0);
    }
  }
  @keyframes slide-out {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(-100%);
    }
  }
.background {
  position: absolute;
  width: 100%;
  top: 0px;
  bottom: 0px;
  background: #eee
}
</style>



