<template>
  <div>
    <transition name='show-zhezhao'>
      <div class="zhezhao" v-show="show_currentevent" @click='show_event'>
      </div>
    </transition>
    <transition name='slide-left'>
      <div class="left-side" v-show="show_currentevent" rel='event'>
        <div id="app" @touchmove.prevent>
          <ttt>
            <template slot="header">
              <h1>{{title}}</h1>
              <p>作者: {{author}}</p>
              <p>发布时间: {{happentime}}</p>
            </template>
            <template slot="content">
              <div v-html="content"></div>
              <button class='btn btn-info' @click='close'>看完啦</button>
            </template>
          </ttt>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import ttt from '@/components/header'
export default {
  components: {
    ttt
  },
  data: () => {
    return {
    }
  },
  computed: {
    show_currentevent: function () {
      return this.$store.state.show_currentevent
    },
    title: function () {
      return this.$store.state.current_event.title
    },
    content: function () {
      return this.$store.state.current_event.event
    },
    author: function () {
      return this.$store.state.current_event.nickname
    },
    happentime: function () {
      return this.$store.state.current_event.happentime
    }
  },
  methods: {
    show_event () {
      this.$store.state.show_currentevent = !this.$store.state.show_currentevent
    },
    close () {
      this.$store.state.show_currentevent = false
    }
  }
}
</script>
<style>
.zhezhao {
  position: fixed;
  height: 100%;
  width: 100%;
  background: #aaa;
  z-index: 1040;
  opacity: .5
}

.left-side {
  position: fixed;
  height: 100%;
  left: 20%;
  right: 20%;
  background: #fff;
  z-index: 1050;
  opacity: 1
}

@media only screen and (max-width: 992px) {
  .left-side {
    left: 10%;
    right:10%;
  }
}

@media only screen and (max-width: 768px) {
  .left-side {
    left: 0%;
    right: 0%;
  }
}


.info-header {
  height: 20%;
  background: #5bc0de;
  text-align: center;
  font-size: 52px;
}

.slide-left-enter-active {
  animation: slide-left-in .5s ease
}

.slide-left-leave-active {
  animation: slide-left-out .5s ease
}

@keyframes slide-left-in {
  0% {
    transform: scale(0)
  }
  100% {
    transform: scale(1);
  }
}

@keyframes slide-left-out {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}

.show-zhezhao-leave-active {
  animation: show-zhezhao-out .4s ease
}


@keyframes show-zhezhao-out {
  0% {}
  100% {}
}
</style>



