<template>
    <button class='btn btn-info tag'>{{txt}}<span class='glyphicon glyphicon-map-marker'></span></button>
</template>

<script>
    export default {
      props: [
        'txt'
      ]
    }
</script>

<style>
  .tag {
        border-width: 3px;
        border-color: #5bc0de;
        border-bottom-color: #eee;
        border-style: dashed
    }
    @media only screen and (max-width: 850px){
        .tag {
            padding: 3px;
            border-width: 5px;
        }
    }
</style>



